#right-panel {

	
	position:absolute;
	height:100%;
	z-index:2;
	background:$c-white;
	width:100vw;
	z-index:1;
	
	.main {
		
		
		overflow-y:hidden;
		overflow-x:scroll;
		width:100vw;
		
		.inner-main {
			margin-left:($g-lr-margin * 7) - $g-gutter;
			position:relative;
			width:10000vw;
			height:100%;
			
			article {
			
				position:absolute;
				width:100vw;
				height:100%;
				top:0;
			
			}
		
		}
		
		
		
	}
	

}